import { createContext, h } from "preact";
import { Router } from "preact-router";
import Header from "./header";
import styled from "styled-components";
import { Footer } from "./Footer";

// Code-splitting is automated for `routes` directory
import Home from "../routes/home";
import Shop from "../routes/shop";
import Page from "../routes/page";

export const ThemeContext = createContext({ theme: { color: "red" } });

const StyledWrapper = styled.div`
	position: relative;
	display: grid;
	grid-template-rows: max-content auto max-content;
	min-height: 100vh;
`;

const App = () => {
	// const theme = useContext(ThemeContext);
	return (
		<ThemeContext.Provider value={{ theme: { color: "red" } }}>
			<ThemeContext.Consumer>
				{({ theme }) => (
					<div id="app">
						<StyledWrapper>
							<Header />
							<Router>
								<Home path="/" />

								<Shop path="/shop" product="none" />
								<Shop path="/shop/:productSlug" />

								<Page path="/terms-and-conditions" page="termsAndConditions" />
								<Page path="/contact" page="contact" />
								<Page path="/privacy-policy" page="privacyPolicy" />
							</Router>
							<Footer />
						</StyledWrapper>
					</div>
				)}
			</ThemeContext.Consumer>
		</ThemeContext.Provider>
	);
};

export default App;
