import { h } from "preact";
import {
	useRef,
	useState,
	useCallback,
	useEffect,
	useContext,
} from "preact/hooks";
import { Link } from "preact-router/match";
import style from "./style.css";
import { products } from "../../assets/data/products";
import { StyledH1 } from "../StyledTypography";
import styled from "styled-components";
import { ThemeContext } from "../app";
import useBreakpoint from "../helpers/useBreakpoint";

const StyledHeader = styled.div`
	--hamburger_width: 2rem;
	--hamburger_width_transition: width 0.5s ease-in-out;
	--white: #efefef;
	--black: #1b1b1b;
	--highlight: rgb(137, 221, 12);
	display: flex;
	justify-content: space-between;
	background: var(--black);
	padding: 1.6rem;

	& h1 {
		color: #efefef;
		text-align: center;
	}

	@media (max-width: 767px) {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		align-items: center;
	}
`;

const StyledHamburger = styled.div`
	@media (max-width: 767px) {
		--hamburger_width: 3.2rem;
		--hamburger_width_transition: width 0.5s ease-in-out;
		--white: #efefef;
		--black: #1b1b1b;
		--highlight: rgb(137, 221, 12);
		position: relative;
		width: var(--hamburger_width);
		height: 2rem;
		z-index: 2;

		&:hover {
			cursor: pointer;
		}

		& div {
			position: absolute;
			top: 1rem;
			width: var(--hamburger_width);
			height: 0.2rem;
			background: var(--white);
			transition: var(--hamburger_width_transition);

			&::before,
			&::after {
				position: absolute;
				content: "";
				width: calc(var(--hamburger_width) - 20%);
				height: 0.2rem;
				background: var(--white);
				transition: var(--hamburger_width_transition);
			}

			&::before {
				top: 1rem;
			}

			&::after {
				bottom: 1rem;
			}
		}

		&:hover div::before,
		&:hover div::after,
		&.open:hover div {
			width: var(--hamburger_width);
		}

		&.open div {
			width: calc(var(--hamburger_width) - 20%);
			transition: var(--hamburger_width_transition);
		}

		&.open div::before,
		&.open div::after {
			width: var(--hamburger_width);
			transition: var(--hamburger_width_transition);
		}
	}
`;

const StyledNav = styled.nav`
	--white: #efefef;
	--highlight: rgb(137, 221, 12);

	display: flex;
	grid-gap: 1.6rem;
	margin: 0.8rem;

	& a {
		text-decoration: none;
		text-transform: uppercase;
		font-weight: 600;
		color: var(--white);
		font-size: 1.6rem;
		cursor: pointer;

		&:hover {
			color: var(--highlight);
		}
	}

	@media (max-width: 767px) {
		&.open {
			position: absolute;
			background: inherit;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			z-index: 1;
			display: flex;
			flex-direction: column;
			justify-content: space-evenly;
			align-items: center;
			border: 1px solid var(--white);
			height: calc(100vh - 15rem);
		}

		&.closed {
			display: none;
		}
	}
`;

const StyledOverlay = styled.div`
	position: absolute;
	z-index: 1;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.75);
`;

const StyledLink = styled(Link)`
	text-decoration: none;
`;

const StyledCart = styled.a`
	text-decoration: none;
	text-transform: uppercase;
	font-weight: 600;
	color: var(--white);
	font-size: 1.6rem;
	cursor: pointer;

	&:hover {
		color: var(--highlight);
	}
`;

const Header = ({ props }) => {
	const [menuVisible, set_menuVisible] = useState(false);

	const _ref = {
		menuVisible,
	};

	const ref = useRef(_ref);
	ref.current = _ref;

	const handleHamburgerClick = useCallback(() => {
		const { menuVisible } = ref.current;
		set_menuVisible(!menuVisible);
	}, []);

	const handleLinkClick = useCallback(() => {
		const { menuVisible } = ref.current;
		if (menuVisible) set_menuVisible(false);
	}, []);

	const breakpoint = useBreakpoint();

	return (
		<StyledHeader>
			<StyledLink underline="none" href="/">
				<StyledH1>Walkers Creek</StyledH1>
			</StyledLink>
			{(breakpoint === "mobileSmall" || breakpoint === "mobile") && (
				<StyledCart className="snipcart-checkout snipcart-summary">
					<i class="fas fa-shopping-cart"></i>
					<span className="snipcart-total-price"></span>
				</StyledCart>
			)}
			<StyledHamburger
				onClick={handleHamburgerClick}
				className={menuVisible ? "open" : "closed"}
			>
				<div />
			</StyledHamburger>
			{menuVisible && <StyledOverlay onClick={() => set_menuVisible(false)} />}{" "}
			<StyledNav className={menuVisible ? "open" : "closed"}>
				<Link onClick={handleLinkClick} activeClassName={style.active} href="/">
					Home
				</Link>

				<Link
					onClick={handleLinkClick}
					activeClassName={style.active}
					href="/shop"
				>
					Shop
				</Link>
				<a className="snipcart-checkout snipcart-summary">
					Cart: <span className="snipcart-total-price"></span>
				</a>
			</StyledNav>
		</StyledHeader>
	);
};

export default Header;
